:root {
  --main-ff: "Inter", sans-serif;
  --second-ff: "RobotoMono", sans-serif;
  --color-dark: #000;
  --color-white: #fff;
  --color-btn-bg: linear-gradient(91.83deg, #095092 0%, #00a7e5 100%);
  --color-light-blue: #00a7e5;
  --color-blue: #0578B9;
  --color-card-grey: #CDCDD1;
  --color-bg: #DDDDE2;
  --border-color: #E1E1E1;
}

@font-face {
  font-weight: 400;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
}

@font-face {
  font-weight: 400;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Regular.woff2") format("woff2"), url("../fonts/RobotoMono-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Medium.woff2") format("woff2"), url("../fonts/RobotoMono-Medium.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Bold.woff2") format("woff2"), url("../fonts/RobotoMono-Bold.woff") format("woff");
}

.button-reset {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border-radius: 0;
  border: none;
  font-family: var(--main-ff);
}

.logo {
  width: 20rem;
}

.nav ul {
  display: flex;
  align-items: center;
}

.nav ul li {
  margin-right: 2.5rem;
}

.nav ul li:last-child {
  margin-right: 0;
}

.nav ul li a {
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
}

.footer-nav ul li {
  margin-bottom: 4.5rem;
}

.footer-nav ul li:last-child {
  margin-bottom: 0;
}

.footer-nav ul li a {
  font-weight: 500;
  font-size: 2.2rem;
  line-height: 3rem;
  font-family: var(--second-ff);
}

.second-font {
  font-weight: 500;
  font-family: var(--second-ff);
}

.second-title {
  font-family: var(--second-ff);
  font-size: 4rem;
  line-height: 4.5rem;
}

.button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 24rem;
  background: var(--color-btn-bg);
  color: var(--color-white);
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
  border-radius: 1rem;
  cursor: pointer;
  overflow: hidden;
}

.button span {
  display: flex;
  align-items: center;
  color: transparent;
  position: relative;
  line-height: 1;
  padding: 1.6rem 0 1.6rem 1.5rem;
  flex-shrink: 0;
}

.button span::before {
  content: attr(data-text);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  position: absolute;
  left: 1rem;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all .3s ease;
  transform: translateY(0) translateZ(0);
}

.button span::after {
  content: attr(data-text);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  position: absolute;
  left: 1rem;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all .3s ease;
  transform: translateY(0) translateZ(0);
}

.button span::after {
  top: 100%;
  opacity: 0;
}

.button__icon {
  fill: var(--color-white);
  margin-right: 1.5rem;
  transition: transform 200ms;
}

.button_white {
  background: var(--color-white);
}

.button_white span::before, .button_white span::after {
  color: var(--color-blue);
}

.button_white .button__icon {
  fill: var(--color-blue);
}

.button_white-black {
  background: var(--color-white);
}

.button_white-black span::before, .button_white-black span::after {
  color: var(--color-dark);
}

.button_white-black .button__icon {
  fill: var(--color-dark);
}

.button:hover span::before {
  opacity: 0;
}

.button:hover span::after {
  opacity: 1;
}

.button:hover span::before, .button:hover span::after {
  transform: translateY(-100%) translateZ(0);
}

.button:hover .button__icon-plus {
  transform: rotate(-90deg);
}

.form {
  width: 100%;
}

.form__heading {
  display: block;
  margin-bottom: 3rem;
  font-size: 2rem;
  line-height: 4.5rem;
}

.form__input-container {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 2rem;
}

.form__input {
  width: 100%;
  background-color: transparent;
  border: 1px solid var(--border-color);
  border-radius: 1rem;
  padding: 1.7rem 1.7rem 1.8rem;
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  outline: none;
}

.form__input::placeholder {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  color: var(--color-dark);
}

.form__textarea {
  width: 100%;
  background-color: transparent;
  border: 1px solid var(--border-color);
  border-radius: 1rem;
  padding: 1.7rem 1.7rem 1.8rem;
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  min-height: 10.6rem;
  resize: none;
  outline: none;
}

.form__textarea::placeholder {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  color: var(--color-dark);
}

.form__button-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form__button-wrapper p {
  max-width: 15.5rem;
  font-size: 1.2rem;
  line-height: 1.4rem;
  color: rgba(111, 111, 111, 0.7);
}

.form__button-wrapper p a {
  text-decoration: underline;
}

.form__button-wrapper p a:hover {
  text-decoration: none;
}

.form__button {
  font-weight: 600;
  min-width: 14.9rem;
}

.form._white .form__input {
  border-color: var(--color-white);
  color: var(--color-white);
}

.form._white .form__input::placeholder {
  color: var(--color-white);
}

.form._white .form__textarea {
  border-color: var(--color-white);
  color: var(--color-white);
}

.form._white .form__textarea::placeholder {
  color: var(--color-white);
}

.form._white .form__button-wrapper p {
  color: var(--color-white);
}

.form._white .form__button {
  background: var(--color-white);
  color: var(--color-dark);
}

.form._white .form__button span::before, .form._white .form__button span::after {
  color: var(--color-dark);
}

.form._white .form__button-icon {
  fill: var(--color-dark);
}

.social {
  display: flex;
  align-items: center;
}

.social__item {
  margin-right: 1.5rem;
}

.social__item:last-child {
  margin-right: 0;
}

.social__link {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  fill: var(--color-dark);
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background-color: var(--color-bg);
  transform: opacity 200ms, visibillty 200ms;
}

.preloader._is-loaded {
  opacity: 0;
  visibility: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  line-height: 1.3;
}

body {
  color: var(--color-dark);
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.9rem;
  font-family: var(--main-ff);
  background-color: var(--color-bg);
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  color: inherit;
  text-decoration: none;
}

h2,
h3,
h4,
h5,
h6 {
  font-family: var(--second-ff);
}

.list-rest {
  padding: 0;
  margin: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  max-width: 1460px;
  margin: 0 auto;
  padding: 0 20px;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
}

.header {
  padding-top: 3rem;
}

.header__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
}

.header_left {
  grid-column: 6 span;
}

.header_right {
  grid-column: 6 span;
  display: flex;
  align-items: center;
}

.header__button {
  display: inline-block;
  margin-left: auto;
  background: url(../img/white-noise.png), var(--color-btn-bg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: var(--color-white);
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
  padding: 0.6rem 1rem;
  border-radius: 2rem;
  cursor: pointer;
}

.footer__container {
  background-color: var(--color-white);
  padding: 3rem;
  border-radius: 20px 20px 0px 0px;
}

.footer__top-block {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  margin-bottom: 13.7rem;
}

.footer__top-block-col:nth-child(1) {
  grid-column: 3 span;
}

.footer__top-block-col:nth-child(2) {
  grid-column: 5 span;
}

.footer__top-block-col:nth-child(3) {
  grid-column: 4 span;
}

.footer__legend {
  display: block;
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: #adadad;
  margin-bottom: 0.1rem;
}

.footer__contact-link {
  display: inline-block;
  font-weight: 500;
  font-size: 3.2rem;
  line-height: 4.8rem;
  font-family: var(--second-ff);
  letter-spacing: -0.04em;
}

.footer__contact-link._phone {
  margin-bottom: 4.3rem;
}

.footer__contact-link._mail {
  margin-bottom: 8.2rem;
  background: var(--color-btn-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.footer__adress {
  display: block;
  font-weight: 400;
  font-size: 2rem;
  line-height: 3rem;
  color: #adadad;
  max-width: 35rem;
}

.footer__bottom-block {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  align-items: end;
}

.footer__bottom-block-col:nth-child(1) {
  grid-column: 8 span;
}

.footer__bottom-block-col:nth-child(2) {
  grid-column: 4 span;
  display: flex;
  align-items: flex-end;
}

.footer__logo {
  display: inline-block;
  margin-bottom: 3rem;
}

.footer__about-comp {
  max-width: 39.5rem;
  margin-bottom: 2.6rem;
  font-size: 1.3rem;
  line-height: 1.8rem;
  color: #A2A2A2;
}

.footer__copyright {
  max-width: 39.5rem;
  font-size: 1.3rem;
  line-height: 1.8rem;
  color: #A2A2A2;
}

.button-scroll-top {
  display: block;
  width: 3.1rem;
  height: 4rem;
  margin-left: auto;
  cursor: pointer;
  transition: fill 200ms;
}

.button-scroll-top:hover {
  fill: var(--color-light-blue);
}

.cards {
  position: relative;
  z-index: 5;
  width: 100%;
  counter-reset: section;
}

.cards__item {
  position: relative;
  z-index: 4;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  background-color: var(--color-white);
  padding: 3rem 2.7rem 1.5rem 2.7rem;
  border-radius: 2rem;
  align-items: flex-end;
}

.cards__item:not(:first-child) {
  position: absolute;
}

.cards__item:nth-child(2) {
  top: 4rem;
  left: 0;
  z-index: 3;
}

.cards__item:nth-child(3) {
  top: 8rem;
  z-index: 2;
}

.cards__item:nth-child(4) {
  top: 12rem;
  z-index: 1;
}

.cards__item:nth-child(5) {
  top: 16rem;
  z-index: 0;
}

.cards__item:nth-child(6) {
  top: 20rem;
  z-index: -1;
}

.cards__item:nth-child(7) {
  top: 24rem;
  z-index: -2;
}

.cards__item:nth-child(2n) {
  background-color: #CDCDD1;
}

.cards__item::before {
  position: absolute;
  left: 2.7rem;
  bottom: 1.6rem;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1.5rem;
  counter-increment: section;
  content: counter(section);
}

.cards__item h3 {
  font-weight: 700;
  font-size: 3rem;
  line-height: 3.5rem;
  font-family: var(--second-ff);
  margin-bottom: 6rem;
}

.cards__item p {
  font-weight: 600;
  font-size: 2rem;
  line-height: 2.5rem;
  margin-bottom: 3.2rem;
  max-width: 38.3rem;
}

.cards__item ul {
  max-width: 34rem;
  margin-bottom: 3.7rem;
}

.cards__item ul li {
  position: relative;
  padding-left: 1.9rem;
  margin-bottom: 2rem;
  font-size: 1.6rem;
  line-height: 1.9rem;
}

.cards__item ul li:last-child {
  margin-bottom: 0;
}

.cards__item ul li::before {
  position: absolute;
  top: .6rem;
  left: 0.2rem;
  width: 0.5rem;
  height: 0.5rem;
  background-color: var(--color-blue);
  border-radius: 50%;
  content: "";
}

.cards__item-customers {
  align-items: stretch;
}

.cards__item-customers .cards__item-left {
  grid-column: 6 span;
}

.cards__item-customers .cards__item-right {
  grid-column: 6 span;
}

.cards__item-customers .cards__item-bottom {
  padding-top: 1.4rem;
}

.cards__item-customers .cards__item-img {
  width: 100%;
  height: 46rem;
  border-radius: 20px;
  border: none;
  margin-left: 0;
  filter: none;
}

.cards__item-customers p {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.9rem;
  margin-bottom: 2rem;
}

.cards__item-customers p:last-child {
  margin-bottom: 0;
}

.cards__item-customers:nth-child(2) {
  top: 4.6rem;
}

.cards__item-customers:nth-child(3) {
  top: 9.2rem;
}

.cards__item-customers:nth-child(4) {
  top: 13.8rem;
}

.cards__item-customers:nth-child(5) {
  top: 18.4rem;
}

.cards__item-customers:nth-child(6) {
  top: 23rem;
}

.cards__item-customers:nth-child(7) {
  top: 27.6rem;
}

.cards__item-link {
  margin-top: auto;
  display: inline-flex;
  align-items: center;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: var(--color-dark);
  margin-bottom: 6.8rem;
}

.cards__item-link-icon {
  display: block;
  margin-left: 2rem;
  fill: var(--color-dark);
}

.cards__item-img-logo {
  display: block;
  margin-bottom: 5.2rem;
}

.cards__item-left {
  grid-column: 4 span;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.cards__item-right {
  grid-column: 8 span;
  display: block;
  align-items: center;
}

.cards__item-bottom {
  grid-column: 12 span;
  padding-top: 6.3rem;
}

.cards__item-legend {
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1.5rem;
  padding-left: 2.3rem;
  text-transform: capitalize;
}

.cards__item-img {
  width: 83rem;
  height: 46.6rem;
  display: block;
  object-fit: cover;
  margin-left: 4rem;
  filter: drop-shadow(10px 10px 54px rgba(0, 0, 0, 0.15));
  border: 4px solid #282828;
  border-radius: 10px;
}

.intro-block {
  padding-top: 7.4rem;
}

.intro-block__container {
  position: relative;
}

.intro-block__content-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column: 20px;
}

.intro-block_left {
  grid-column: 6 span;
  margin-bottom: 33rem;
}

.intro-block_right {
  position: relative;
  grid-column: 6 span;
}

.intro-block__content h1 {
  font-weight: 700;
  font-size: 6rem;
  line-height: 6rem;
  max-width: 62.4rem;
  margin-bottom: 7.3rem;
  font-family: var(--second-ff);
}

.intro-block__content p {
  font-size: 2rem;
  line-height: 2.5rem;
  max-width: 34rem;
}

.intro-block__img {
  position: absolute;
  bottom: 9.4rem;
  right: 0;
  display: block;
  width: 71.4rem;
  height: 54.5rem;
  object-fit: cover;
}

.contact-us {
  margin-bottom: 15rem;
}

.contact-us__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
}

.contact-us__feautures-block {
  position: relative;
  z-index: 1;
  grid-column: 8 span;
  background: var(--color-btn-bg);
  padding: 2.2rem 2.9rem;
  border-radius: 2rem;
  min-height: 70rem;
  overflow: hidden;
}

.contact-us__feautures-block::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  content: "";
}

.contact-us__form-block {
  grid-column: 4 span;
  padding: 3.3rem 4rem 4.2rem;
  border-radius: 2rem;
  background: var(--color-btn-bg);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contact-us__video-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: var(--color-dark);
  opacity: 0;
}

.contact-us__video-wrapper.is-active {
  z-index: 4;
  opacity: 1;
}

.contact-us__video-bg {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.contact-us__play-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  transform: translate(-50%, -50%);
  width: 8.5rem;
  height: 8.5rem;
  fill: var(--color-white);
  transition: fill 200ms, transform 200ms;
  cursor: pointer;
}

.contact-us__play-btn:hover {
  fill: var(--color-light-blue);
}

.contact-us__play-btn:active {
  transform: translate(-50%, -50%) scale(0.9);
}

.contact-us__form {
  margin-top: auto;
}

.contact-us__legend {
  display: block;
  font-weight: 700;
  margin-bottom: 2.5rem;
  color: var(--color-white);
}

.contact-us__form-text {
  font-weight: 600;
  font-size: 2rem;
  line-height: 2.4rem;
  color: var(--color-white);
}

.contact-us__content h2 {
  font-weight: 700;
  font-size: 4rem;
  line-height: 4.5rem;
  text-transform: capitalize;
  max-width: 63.5rem;
  margin-bottom: 4rem;
  color: var(--color-white);
}

.contact-us__content p {
  font-weight: 600;
  font-size: 2rem;
  line-height: 2.4rem;
  max-width: 33.7rem;
  color: var(--color-white);
}

.retailers {
  margin-bottom: 27rem;
}

.retailers__heading-wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  margin-bottom: 5.1rem;
}

.retailers__left-block {
  grid-column: 6 span;
}

.retailers__right-block {
  grid-column: 6 span;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.retailers__content h2 {
  font-size: 4rem;
  line-height: 4.5rem;
  max-width: 46rem;
}

.retailers__content p {
  font-size: 1.6rem;
  line-height: 2rem;
  color: #A6A6B0;
  max-width: 23.9rem;
}

.retailers__content p b {
  color: var(--color-dark);
  display: block;
}

.feautures {
  margin-bottom: 9rem;
}

.feautures__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 20px;
}

.feautures__item {
  position: relative;
  grid-column: 4 span;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 35.5rem;
  background-color: var(--color-white);
  padding: 3rem;
  border-radius: 2rem;
  cursor: pointer;
  transition: box-shadow 200ms;
}

.feautures__item._transparent {
  background-color: transparent;
  padding: 0;
  cursor: default;
}

.feautures__item._request-demo {
  background: var(--color-btn-bg);
  cursor: default;
}

.feautures__item:not(._transparent):not(._request-demo):hover {
  z-index: 1;
  box-shadow: 0px 120px 120px rgba(22, 34, 51, 0.08), 0px 64px 64px rgba(22, 34, 51, 0.12), 0px 32px 32px rgba(22, 34, 51, 0.04), 0px 24px 24px rgba(22, 34, 51, 0.04), 0px 4px 24px rgba(22, 34, 51, 0.04), 0px 4px 4px rgba(22, 34, 51, 0.04);
}

.feautures__item:not(._transparent):not(._request-demo):hover .feautures__item-img {
  transform: translateY(-5rem) scale(1.7);
}

.feautures__content h2 {
  font-size: 4rem;
  line-height: 4.5rem;
  margin-bottom: 2.5rem;
}

.feautures__content p {
  font-size: 1.6rem;
  line-height: 2.1rem;
  color: #A6A6B0;
}

.feautures__item-img {
  position: absolute;
  top: 3.8rem;
  right: 3rem;
  display: block;
  width: 10rem;
  height: 8rem;
  transition: transform 200ms;
}

.feautures__item-heading {
  display: block;
  max-width: 27rem;
  font-weight: 500;
  font-size: 2rem;
  line-height: 2.5rem;
  font-family: var(--second-ff);
}

.feautures__item-text {
  max-width: 37.2rem;
  font-size: 1.4rem;
  line-height: 1.6rem;
  margin-top: auto;
}

.feautures__item-request-heading {
  font-weight: 500;
  font-size: 3.2rem;
  line-height: 3.5rem;
  text-transform: capitalize;
  font-family: var(--second-ff);
  color: var(--color-white);
}

.feautures__item-button {
  margin-top: auto;
}

.customers {
  margin-bottom: 20.2rem;
}

.customers__heading-wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  margin-bottom: 7.1rem;
}

.customers__left-block {
  grid-column: 6 span;
}

.customers__right-block {
  grid-column: 6 span;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.customers__content h2 {
  font-size: 4rem;
  line-height: 4.5rem;
  max-width: 46rem;
}

.customers__count-block {
  display: flex;
}

.customers__count-block-item {
  margin-right: 6.2rem;
}

.customers__count-block-item:last-child {
  margin-right: 0;
}

.customers__count-block-item span {
  position: relative;
  display: block;
  font-weight: 400;
  font-size: 7.2rem;
  line-height: 1;
  font-family: var(--second-ff);
  margin-bottom: 1rem;
  background: var(--color-btn-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.customers__count-block-item span sup {
  display: block;
  position: absolute;
  top: 1rem;
  right: 1.4rem;
  font-size: 2.5rem;
  line-height: 1;
  background: var(--color-btn-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.customers__count-block-item p {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 1.7rem;
  font-family: var(--second-ff);
  max-width: 12.5rem;
}
